import { CangjieInputEvent, Controller } from '@ali/4ever-cangjie';
import onSpace from './onSpace';

export default function onCangjieInput(
  event: CangjieInputEvent,
  controller: Controller,
  next: Function,
) {
  if (
    event.detail.data !== ' ' ||
    controller.query('isSelectionInSubjectHeading')
  )
    return next();
  return onSpace(event, controller, next);
}
